export default {
  state: () => ({
    navbar_title: "PROJECTS",

    previus_page: null,

    const_task: [
      { id: 'todo', label: 'TODO' },
      { id: 'in_process', label: 'IN PROCESS' },
      { id: 'done', label: 'DONE' }
    ],

    const_materials: [
      { id: 'order', label: 'ORDERS' },
      { id: 'in_process', label: 'IN PROCESS' },
      { id: 'done', label: 'DONE' }
    ],

    const_personal: [
      { id: 'free', label: 'FREE' },
      { id: 'ocupied', label: 'OCUPIED' },
      { id: 'not_available', label: 'NOT AVAILABLE' }
    ],
    
    category_routes_mapping: {
      tasks: "SET_MANAGEMENT_TO_TASKS",
      materials: "SET_MANAGEMENT_TO_MATERIALS",
      personal: "SET_MANAGEMENT_TO_PERSONAL"
    }
  }),

  mutations: {
    SET_NAVBAR_TITLE(state, title) {
      state.navbar_title = title;
    },

    SET_PREVIUS_PAGE(state, previus_page) {
      state.previus_page = previus_page;
    }
  },

  actions: {
    SET_MANAGEMENT_TO_TASKS({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_task)
      commit('management/SET_CURRENT_CATEGORY', "tasks")
      commit(`SET_NAVBAR_TITLE`, 'TASKS')
    },

    SET_MANAGEMENT_TO_MATERIALS({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_materials)
      commit('management/SET_CURRENT_CATEGORY', "materials")
      commit(`SET_NAVBAR_TITLE`, 'MATERIALS')
    },

    SET_MANAGEMENT_TO_PERSONAL({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_personal)
      commit('management/SET_CURRENT_CATEGORY', "personal")
      commit(`SET_NAVBAR_TITLE`, 'PERSONAL')
    },
  },

  getters: {},
}